@use '../mixins' as *;
/*
Basic Figure Customized(2021.11.01~)

Styleguide 23

*/

/* topページ blog
********************************************/

.ec-top-blog {
  position: relative;

  @include container;
  margin-top: 30px;
  @include media_desktop {
    margin-top: 50px;
  }

  &__btn {
    position: absolute;
    top: 5px;
    right: 15px;

    a {
      font-weight: 800;
      color: #525263;

      &:hover {
        color: #aaa;
      }
    }
  }

  .row {
    display: flex;
    flex-wrap: wrap;

    .col-3 {
      flex: 0 0 100%;
      padding: 0 15px;

      @include media_desktop {
        flex: 0 0 33.333%;
      }

      & + .col-3 {
        margin-top: 20px;

        @include media_desktop {
          margin-top: 0;
        }
      }

      a {
        display: flex;
        width: 100%;
        color: $bl;

        @include media_desktop {
          display: block;
        }

        .thumbnail {
          flex: 0 0 120px;
          width: 120px;
          height: 120px;
          margin: 0;
          padding: 0;
          border: none;
          aspect-ratio: 1/1;
          overflow: hidden;

          @include media_desktop {
            flex: none;
            width: 100%;
            height: auto;
          }

          img {
            width: 100%;
            min-width: 100%;
            height: 100%;
            min-height: 100%;
            object-fit: cover;
          }
        }

        .text-box {
          padding: 8px 0 0 16px;

          @include media_desktop {
            padding: 0;
          }

          .meta {
            display: flex;
            justify-content: space-between;
            align-items: center;

            @include media_desktop {
              padding-top: 8px;
            }

            .category {
              margin-left: 15px;
              padding: 3px 12px;
              @include font(14px,500,1,0);
              background-color: #ddd;
              color: #fff;

              @include media_desktop {
                margin-left: 0;
                padding: 5px 12px;
              }

              &_ice {
                background-color: #ef857d;
              }

              &_coffee {
                background-color: #6a3906;
              }

              &_bread {
                background-color: #f8b62d;
              }
            }
          }

          .title {
            margin-top: 10px;
            font-weight: bold;
          }
        }
      }
    }
  }
}


/* topページ links
********************************************/

.ec-top-links {
  margin: 30px 0 0;

  @include media_desktop {
    margin: 50px calc(50% - 50vw);
  }

  @media (min-width:1600px) {
    margin: 50px -235px;
  }


  &__inner {
    display: flex;
    flex-wrap: wrap;
    max-width: 1600px;
    margin: 0 auto;
  }

  .ec-top-links-item {
    flex: 0 0 100%;
    aspect-ratio: 20 / 9;
    user-select: none;

    @include media_desktop {
      flex: 0 0 50%;
    }

    a {
      display: block;

      & > * {
        transition: .3s;
      }

      &:hover * {
        opacity: .8;
        text-decoration: none;
      }
    }

    &__inner {
      display: flex;
      align-items: center;
      position: relative;
      width: 100%;
      padding: 0 0 0 40px;
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
      aspect-ratio: 20 / 9;

      @include media_desktop {
        justify-content: center;
        padding: 0;
      }
    }

    &__title {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      width: 130px;
      padding: 0 8px;
      font-size: 16px;
      background-color: rgba(255,255,255,.8);
      color: #555;
      aspect-ratio: 1/1;

      @include media_desktop {
        width: 27%;
        padding: 0;
        font-size: 18px;
      }

      span {
        display: block;
        margin-top: 7px;
        font-size: 13px;
      }
    }

    &__img {
      margin-top: 20px;
      text-align: center;

      img {
        width: auto;
        max-width: 100%;
        height: auto;
      }
    }
  }
}


/* topページ instagram
********************************************/

.ec-top-instagram {
  margin-top: 30px;
  padding: 0 5.333%;

  @include media_desktop {
    margin-top: 50px;
    padding: 0;
  }
}


/* topページ バナー
********************************************/

.ec-top-banner {
  margin-top: 40px;
  text-align: center;

  a {

    &:hover {
      opacity: .7;
    }
  }
}
