@use '../mixins' as *;

/*
商品詳細

商品詳細ページに関する Project コンポーネントを定義します。

ex [商品詳細ページ](http://demo3.ec-cube.net/products/detail/18)


Markup:
include /assets/tmpl/elements/14.1.product.pug
+ec-productSimpleRole

Styleguide 14.1
*/
.ec-productRole {
  @include container;

  &__img {
    margin-right: 0;
    margin-bottom: 20px;
    @include media_desktop {
      margin-right: 16px;
      margin-bottom: 0;
    }
  }

  &__profile {
    margin: 0;
  }

  &__title {
    .ec-headingTitle {
      font-size: 20px;
      @include media_desktop {
        font-size: 32px;
      }
    }
  }

  &__tags {
    margin-top: 16px;
    padding: 0;
    padding-bottom: 16px;
    border-bottom: 1px dotted #ccc;
  }

  &__tag {
    display: inline-block;
    padding: 2px 5px;
    list-style: none;
    font-size: 80%;
    color: #525263;
    border: solid 1px #D7DADD;
    border-radius: 3px;
    background-color: #F5F7F8;
  }

  &__priceRegular {
    padding-top: 14px
  }

  &__priceRegularTax {
    margin-left: 5px;
    font-size: 10px;
  }

  &__price {
    color: #DE5D50;
    font-size: 28px;
    padding: 0;
    border-bottom: 0;
    @include media_desktop {
      padding: 14px 0;
      border-bottom: 1px dotted #ccc;
    }
  }

  &__code {
    padding: 14px 0;
    border-bottom: 1px dotted #ccc;
  }

  &__category {
    padding: 14px 0;
    border-bottom: 1px dotted #ccc;
    a {
      color: #33A8D0;
    }
    ul {
      list-style: none;
      padding: 0;
      margin: 0;
    }
  }

  &__actions {
    padding: 14px 0;
    .ec-select {
      select {
        height: 40px;
        max-width: 100%;
        min-width: 100%;
        @include media_desktop {
          min-width: 350px;
          max-width: 350px;
        }
      }
    }
  }

  &__btn {
    width: 100%;
    margin: 0 auto 10px;

    @include media_desktop {
      width: 60%;
      margin: 0 auto 16px;
      min-width: 350px;
    }
  }

  &__description {
    margin-bottom: 16px;
  }
}


//商品詳細
.ec-productRole {

  .ec-grid2 {
    display: block !important;

    .kokokara_select_product_left {
      width: 100%;
      margin-top: 20px;

      .ec-sliderItemRole {
        max-width: 50%;
        margin: 30px auto 0;
      }
    }

    .kokokara_select_product_right {
      width: 100%;
    }
  }
}


//おいしいお召し上がり方
.ec-modal-02 {
  display: flex;
  justify-content: center;
  margin-top: 30px;

  @include media_desktop {
    margin-top: 50px;
  }

  &__text {
    flex: 0 0 40%;
    min-width: 260px;
    padding: 20px 30px;
    border: 2px solid crimson;
    @include font(20px,800,1,.1em);
    text-align: center;
    cursor: pointer;
    user-select: none;
    transition: .2s;

    &:hover {
      opacity: .7;
    }
  }
}
